document.addEventListener("DOMContentLoaded", function () {
  startApp();
});

function startApp() {
  staticNav();
  createGallery();
  scrollNav();
}

function staticNav() {
  const navbar = document.querySelector(".header");
  const aboutFestival = document.querySelector(".about-festival");
  const body = document.querySelector("body");
  window.addEventListener("scroll", function () {
    if (aboutFestival.getBoundingClientRect().bottom < 0) {
      navbar.classList.add("static");
      body.classList.add('body-scroll')
    } else {
      navbar.classList.remove("static");
      body.classList.remove('body-scroll')
    }
  });
}

function scrollNav() {
  const links = document.querySelectorAll(".main-nav");

  links.forEach((link) => {
    link.addEventListener("click", function (e) {
      e.preventDefault();
      const info = e.target.attributes.href.value;
      const section = document.querySelector(info);

      section.scrollIntoView({ behavior: "smooth" });
    });
  });
}

function createGallery() {
  const gallery = document.querySelector(".gallery-images");

  for (let i = 1; i <= 12; i++) {
    const image = document.createElement("picture");
    image.innerHTML = `
    
    <source srcset="build/img/thumb/${i}.avif" type="image/avif" />
    <source srcset="build/img/thumb/${i}.webp" type="image/webp" />
    <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="gallery-image"/>
    `;

    image.onclick = function () {
      showImage(i);
    };

    gallery.appendChild(image);
  }
}

function showImage(id) {
  const image = document.createElement("picture");
  image.innerHTML = `
    
    <source srcset="build/img/grande/${id}.avif" type="image/avif" />
    <source srcset="build/img/grande/${id}.webp" type="image/webp" />
    <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="gallery-image"/>
    `;

  const overlay = document.createElement("div");
  overlay.appendChild(image);
  overlay.classList.add("overlay");
  overlay.onclick = function () {
    const body = document.querySelector("body");
    body.classList.remove("static-body");
    overlay.remove();
  };

  // Button to close Modal
  const closeModal = document.createElement("p");
  closeModal.textContent = "X";
  closeModal.classList.add("btn-cerrar");
  closeModal.onclick = function () {
    const body = document.querySelector("body");
    body.classList.remove("static-body");
    overlay.remove();
  };

  overlay.appendChild(closeModal);

  const body = document.querySelector("body");
  body.appendChild(overlay);
  body.classList.add("static-body");
}
